body select:hover {
    opacity: 0.8;
}

body select:active {
    opacity: 1.0;
}

.my-panel {
    background-color: #222222;
    color: #F3EFE0;
}

.my-panel-el {
    background-color: #434242;
}

.my-buttons {
    background-color: #222222;
    color: #F3EFE0;
    border-style: none;
    font-size: 15px;
    font-weight: bold;
    text-shadow: 2px 2px rgba(0,0,0,2);
    border-radius: 5px;
}

.my-buttons:hover {
   opacity: 0.8;
}

.my-buttons:active {
    opacity: 1.0;
}

.button-center {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}

.Console_text_ok {
    color: green;
}

.Console_text_nok {
    color: #cc0000;
}

#Console {
    position: fixed;
    bottom: -100px;
    right: 0;
    opacity: 0.2;
    transition-duration: 0.2s;
    width: 300px;
    height: 200px;
    color: #F3EFE0;
    background-color: rgba(0,0,0,0.8);
    padding: 10px;
}

#Console:hover {
    opacity: 1.0;
    bottom: 0;
}

#Console_title p {
    margin: 0;
    background-color: rgba(0,0,0,0.9);
    text-align: center;
}
